import * as path from "node:path";
import fs from "node:fs/promises";
import { sqliteTable, int, text, integer, index } from "drizzle-orm/sqlite-core";
import { sql, relations } from "drizzle-orm";
const BASE_PATH = "data";
const SETTINGS_DIR = path.join(BASE_PATH, "settings");
const KEY_FILE = path.join(BASE_PATH, ".secret_key");
path.join(BASE_PATH, "sessions");
const USER_DIR = path.join(BASE_PATH, "users");
const STACKS_DIR = path.join(BASE_PATH, "stacks");
async function ensureDirectory(dir, mode = 493) {
  try {
    await fs.mkdir(dir, { recursive: true, mode });
  } catch (error) {
    console.error(`Failed to ensure directory ${dir}:`, error);
    throw error;
  }
}
const settingsTable = sqliteTable("settings_table", {
  id: int().primaryKey({ autoIncrement: true }),
  dockerHost: text().notNull(),
  stacksDirectory: text().notNull(),
  autoUpdate: integer({ mode: "boolean" }).notNull().default(false),
  autoUpdateInterval: int().notNull().default(300),
  pollingEnabled: integer({ mode: "boolean" }).notNull().default(true),
  pollingInterval: int().notNull().default(5),
  pruneMode: text({ enum: ["all", "dangling"] }),
  registryCredentials: text({ mode: "json" }).notNull().default("[]"),
  templateRegistries: text({ mode: "json" }).notNull().default("[]"),
  auth: text({ mode: "json" }).notNull(),
  onboarding: text({ mode: "json" }),
  baseServerUrl: text(),
  maturityThresholdDays: int().notNull().default(30),
  createdAt: int({ mode: "timestamp" }).notNull().default(sql`(unixepoch())`),
  updatedAt: int({ mode: "timestamp" }).notNull().default(sql`(unixepoch())`)
});
const usersTable = sqliteTable("users_table", {
  id: text("id").primaryKey(),
  username: text("username").notNull().unique(),
  passwordHash: text("password_hash"),
  displayName: text("display_name"),
  email: text("email"),
  roles: text("roles", { mode: "json" }).notNull().default("[]"),
  requirePasswordChange: integer("require_password_change", { mode: "boolean" }).notNull().default(false),
  oidcSubjectId: text("oidc_subject_id"),
  lastLogin: int("last_login", { mode: "timestamp" }),
  createdAt: int("created_at", { mode: "timestamp" }).notNull().default(sql`(unixepoch())`),
  updatedAt: int("updated_at", { mode: "timestamp" }).notNull().default(sql`(unixepoch())`)
});
const stacksTable = sqliteTable("stacks_table", {
  id: text("id").primaryKey(),
  name: text("name").notNull().unique(),
  dirName: text("dir_name"),
  path: text("path").notNull(),
  autoUpdate: integer("auto_update", { mode: "boolean" }).notNull().default(false),
  isExternal: integer("is_external", { mode: "boolean" }).notNull().default(false),
  isLegacy: integer("is_legacy", { mode: "boolean" }).notNull().default(false),
  isRemote: integer("is_remote", { mode: "boolean" }).notNull().default(false),
  agentId: text("agent_id"),
  // For remote stacks
  agentHostname: text("agent_hostname"),
  // For remote stacks
  status: text("status", { enum: ["running", "stopped", "partially running", "unknown"] }).notNull().default("unknown"),
  serviceCount: int("service_count").notNull().default(0),
  runningCount: int("running_count").notNull().default(0),
  composeContent: text("compose_content"),
  envContent: text("env_content"),
  lastPolled: int("last_polled", { mode: "timestamp" }),
  createdAt: integer("created_at", { mode: "timestamp" }).notNull().default(sql`(unixepoch())`),
  updatedAt: integer("updated_at", { mode: "timestamp" }).notNull().default(sql`(unixepoch())`)
});
const agentsTable = sqliteTable("agents_table", {
  id: text("id").primaryKey(),
  hostname: text("hostname").notNull(),
  platform: text("platform").notNull(),
  version: text("version").notNull(),
  capabilities: text("capabilities", { mode: "json" }).notNull().default("[]"),
  status: text("status", { enum: ["online", "offline"] }).notNull().default("offline"),
  lastSeen: integer("last_seen", { mode: "timestamp" }).notNull(),
  registeredAt: integer("registered_at", { mode: "timestamp" }).notNull(),
  // Metrics (optional)
  containerCount: int("container_count"),
  imageCount: int("image_count"),
  stackCount: int("stack_count"),
  networkCount: int("network_count"),
  volumeCount: int("volume_count"),
  // Docker info (optional)
  dockerVersion: text("docker_version"),
  dockerContainers: int("docker_containers"),
  dockerImages: int("docker_images"),
  // Metadata (optional JSON object)
  metadata: text("metadata", { mode: "json" }),
  createdAt: int("created_at", { mode: "timestamp" }).notNull().default(sql`(unixepoch())`),
  updatedAt: int("updated_at", { mode: "timestamp" }).notNull().default(sql`(unixepoch())`)
});
const agentTasksTable = sqliteTable("agent_tasks_table", {
  id: text("id").primaryKey(),
  agentId: text("agent_id").notNull(),
  type: text("type", {
    enum: ["docker_command", "stack_deploy", "image_pull", "health_check", "container_start", "container_stop", "container_restart", "container_remove", "agent_upgrade"]
  }).notNull(),
  payload: text("payload", { mode: "json" }).notNull(),
  status: text("status", { enum: ["pending", "running", "completed", "failed"] }).notNull().default("pending"),
  result: text("result", { mode: "json" }),
  error: text("error"),
  // Execution timing
  startedAt: integer("started_at", { mode: "timestamp" }),
  completedAt: integer("completed_at", { mode: "timestamp" }),
  createdAt: integer("created_at", { mode: "timestamp" }).notNull().default(sql`(unixepoch())`),
  updatedAt: integer("updated_at", { mode: "timestamp" }).notNull().default(sql`(unixepoch())`)
});
const agentTokensTable = sqliteTable("agent_tokens_table", {
  id: text("id").primaryKey(),
  agentId: text("agent_id").notNull(),
  token: text("token").notNull().unique(),
  // Hashed token for authentication
  name: text("name"),
  // Human-readable name for the token
  permissions: text("permissions", { mode: "json" }).notNull().default("[]"),
  // JSON array of permissions
  lastUsed: integer("last_used", { mode: "timestamp" }),
  expiresAt: integer("expires_at", { mode: "timestamp" }),
  // Optional expiration
  isActive: integer("is_active", { mode: "boolean" }).notNull().default(true),
  createdAt: integer("created_at", { mode: "timestamp" }).notNull().default(sql`(unixepoch())`),
  updatedAt: integer("updated_at", { mode: "timestamp" }).notNull().default(sql`(unixepoch())`)
});
const deploymentsTable = sqliteTable("deployments_table", {
  id: text("id").primaryKey(),
  name: text("name").notNull(),
  type: text("type", { enum: ["stack", "container", "image"] }).notNull(),
  status: text("status", { enum: ["pending", "running", "stopped", "failed", "completed"] }).notNull().default("pending"),
  agentId: text("agent_id").notNull(),
  taskId: text("task_id"),
  // Links to agentTasksTable
  error: text("error"),
  // Metadata as JSON to match your interface
  metadata: text("metadata", { mode: "json" }),
  createdAt: int("created_at", { mode: "timestamp" }).notNull().default(sql`(unixepoch())`),
  updatedAt: int("updated_at", { mode: "timestamp" }).notNull().default(sql`(unixepoch())`)
});
const imageMaturityTable = sqliteTable(
  "image_maturity_table",
  {
    id: text("id").primaryKey(),
    // This will be the image ID
    repository: text("repository").notNull(),
    tag: text("tag").notNull(),
    currentVersion: text("current_version").notNull(),
    latestVersion: text("latest_version"),
    status: text("status", { enum: ["Matured", "Not Matured", "Unknown"] }).notNull().default("Unknown"),
    updatesAvailable: integer("updates_available", { mode: "boolean" }).notNull().default(false),
    // Dates and timing
    currentImageDate: int("current_image_date", { mode: "timestamp" }),
    latestImageDate: int("latest_image_date", { mode: "timestamp" }),
    daysSinceCreation: int("days_since_creation"),
    // Registry information
    registryDomain: text("registry_domain"),
    isPrivateRegistry: integer("is_private_registry", { mode: "boolean" }).notNull().default(false),
    // Check metadata
    lastChecked: int("last_checked", { mode: "timestamp" }).notNull(),
    checkCount: int("check_count").notNull().default(1),
    lastError: text("last_error"),
    // Performance tracking
    responseTimeMs: int("response_time_ms"),
    createdAt: int("created_at", { mode: "timestamp" }).notNull().default(sql`(unixepoch())`),
    updatedAt: int("updated_at", { mode: "timestamp" }).notNull().default(sql`(unixepoch())`)
  },
  (table) => [
    // Single column indexes
    index("idx_image_maturity_repository").on(table.repository),
    index("idx_image_maturity_status").on(table.status),
    index("idx_image_maturity_updates").on(table.updatesAvailable),
    index("idx_image_maturity_last_checked").on(table.lastChecked),
    // Composite indexes for common query patterns
    index("idx_image_maturity_repo_tag").on(table.repository, table.tag),
    index("idx_image_maturity_status_updates").on(table.status, table.updatesAvailable),
    // Index for cleanup queries (registry domain filtering)
    index("idx_image_maturity_registry").on(table.registryDomain),
    // Index for performance monitoring
    index("idx_image_maturity_check_count").on(table.checkCount)
  ]
);
relations(agentsTable, ({ many }) => ({
  tasks: many(agentTasksTable),
  tokens: many(agentTokensTable),
  remoteStacks: many(stacksTable),
  deployments: many(deploymentsTable)
}));
relations(agentTasksTable, ({ one, many }) => ({
  agent: one(agentsTable, {
    fields: [agentTasksTable.agentId],
    references: [agentsTable.id]
  }),
  deployments: many(deploymentsTable)
}));
relations(agentTokensTable, ({ one }) => ({
  agent: one(agentsTable, {
    fields: [agentTokensTable.agentId],
    references: [agentsTable.id]
  })
}));
relations(stacksTable, ({ one }) => ({
  agent: one(agentsTable, {
    fields: [stacksTable.agentId],
    references: [agentsTable.id]
  })
}));
relations(deploymentsTable, ({ one }) => ({
  agent: one(agentsTable, {
    fields: [deploymentsTable.agentId],
    references: [agentsTable.id]
  }),
  task: one(agentTasksTable, {
    fields: [deploymentsTable.taskId],
    references: [agentTasksTable.id]
  })
}));
relations(usersTable, ({ many }) => ({
  // Add user relations if needed in the future
}));
relations(settingsTable, ({ many }) => ({
  // Settings is typically a singleton, no relations needed
}));
export {
  BASE_PATH as B,
  KEY_FILE as K,
  STACKS_DIR as S,
  USER_DIR as U,
  agentsTable as a,
  agentTasksTable as b,
  SETTINGS_DIR as c,
  deploymentsTable as d,
  ensureDirectory as e,
  stacksTable as f,
  imageMaturityTable as i,
  settingsTable as s,
  usersTable as u
};
